// csv query for students

import * as d3 from 'd3';
import Student_list from '../../components/student_list.vue';

// 根据入学年份查询学生列表
export async function findAllStudents() {
    const decoder = new TextDecoder('utf-8');
    let response = await fetch('/data/students.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT year, name, id FROM ? WHERE isDel = '0' order by year desc, name asc", [parsedData]);

    let student_list = []
    let year_list = []

    for (let i = 0; i < result.length; i++) {
        const item = result[i];
        let year = item.year
        let name = item.name
        let id = item.id
        if (year_list.indexOf(year) == -1) {
            year_list.push(year)
        }
        let student_json = {
            "year": year,
            "name": name,
            "id": id
        }
        student_list.push(student_json)
    }

    let re_list = []
    for (let i = 0; i < year_list.length; i++) {
        const element = year_list[i];
        let students = []
        for (let j = 0; j < student_list.length; j++) {
            const student = student_list[j];
            if (student["year"] == element) {
                let student_info = {
                    "name": student["name"],
                    "id": student["id"]
                }
                students.push(student_info)
            }
        }
        let stu = {
            "year": element,
            "students": students,
        }
        re_list.push(stu)
    }

    return re_list;
}

// 根据id查询学生信息
export async function findStudentById(id) {
    const decoder = new TextDecoder('utf-8');
    let response = await fetch('/data/students.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql(`SELECT * FROM ? WHERE isDel = '0' and id='${id}'`, [parsedData]);

    return result[0];
}

